.app__navbar{
 width:100%;
 display:flex;
 align-items: center;
 justify-content: space-between;
padding: 1rem 2rem;
background: rgba(255,255,255,0.25);
backdrop-filter: blur(4px);
--webkit-backdrop-filter: blur(4px);
border: 1px solid rgba(255,255,255,0.18) ;
position: fixed;
z-index: 2;
}

.app__navbar-logo{
 display: flex;
 align-items: center;
 justify-content: flex-start;
 a{
  font-size: 30px;
  text-decoration: none;
  color: var(--gray-color);
 }
}


.app__navbar-links{
 flex: 1;
 display: flex;
 justify-content: center;
 align-items: center;
 list-style: none;

 li{
  margin: 0 1rem;
  cursor: pointer;
  flex-direction: column;

  div{
   width: 5px;
   height: 5px;
   background: transparent;
   border-radius: 50%;
   margin-bottom: 5px;
  }
  a{
   color: var(--gray-color);
   text-decoration: none;
   flex-direction: column;
   text-transform: uppercase;
   font-weight: 500;
   transition: all 0.3s ease-in-out;
   font-size: 18px;

   &:hover{
    color: var(--secondary-color);
   }
  }

  &:hover{
   div{
    background-color: var(--secondary-color);
   }
  }

 }

 @media screen and (max-width:900px) {
  display: none;
 }
}

.app__nav-btn{
 padding: 10px 15px;
 font-weight: 500;
 font-size: 16px;
 background-color: var(--gray-color);
 color: rgb(248, 247, 247);
 border: none;
 border-radius: 5px;
cursor: pointer;
transition: all 0.3s ease;

&:hover{
  background-color: white;
  color: var(--gray-color);
  
  font-weight: 600;
  border: 2px solid var(--gray-color);
}

@media screen and (max-width:900px) {
 display: none;
}
}


.app__navbar-menu{
 width: 35px;
 height: 35px;
 border-radius: 50%;
 position: relative;

 display: flex;
 justify-content: center;
 align-items: center;
 background: var(--secondary-color);

 svg{
  width: 70%;
  height: 70%;
  color: var(--white-color)
 }
 div{
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  z-index: 5;

  padding: 1rem;

  width: 80%;
  height: 100vh;

  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  flex-direction: column;

  background: url("../../images/bgWhite.png");
  background-color: var(--white-color);
  background-size: cover;
  background-repeat: repeat;

  box-shadow: 0px 0px 20px rgba(168,168,168,0.15);

  svg{
   width: 35px;
   height: 35px;
   color: var(--secondary-color);
   margin: 0.5rem 1rem;
  }

  ul{
   list-style: none;
   margin: 0;
   padding: 0;
   height: 100%;
   width: 100%;
   display: flex;
   justify-content: flex-start;
   align-items: flex-start;
   flex-direction: column;

   li{
    margin: 1rem;
    a {
     color: var(--gray-color);
     text-decoration: none;
     font-size: 1rem;
     text-transform: uppercase;
     font-weight: 500;

     transition: all 0.3s ease-in-out;

     &:hover {
       color: var(--secondary-color);
     }
   }
   }
  }
  @media screen and (min-width: 900px) {
   display: none;
 }
 }
 @media screen and (min-width: 900px) {
  display: none;
}
}